body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 600px;
  margin: auto;
  border: 1px solid #ddd;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
}

.header {
  background-color: #c62828; /* red */
  color: white;
  padding: 1rem;
  font-size: 1.3rem;
  font-weight: bold;
  text-align: center;
}

.chat-container {
  flex: 1;
  padding: 1rem;
  background-color: #fff;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.message {
  max-width: 80%;
  padding: 0.75rem 1rem;
  border-radius: 20px;
  line-height: 1.4;
  font-size: 0.95rem;
}

.user {
  align-self: flex-end;
  background-color: #ff8a80;
  color: #000;
}

.bot {
  align-self: flex-start;
  background-color: #ffcdd2;
  color: #000;
}

.input-container {
  display: flex;
  padding: 0.5rem;
  border-top: 1px solid #ddd;
  background-color: #fff;
}

input[type="text"] {
  flex: 1;
  padding: 0.5rem 1rem;
  border: 1px solid #ccc;
  border-radius: 20px;
  font-size: 1rem;
  outline: none;
}

button {
  margin-left: 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  background-color: #c62828; /* red */
  color: white;
  border-radius: 20px;
  font-size: 1rem;
  cursor: pointer;
}

button:hover {
  background-color: #b71c1c;
}
